// let count = 1 do not change state directly on reducer.
function reducer (state = initialState, action) {
    console.log(state, action)
    switch (action.type) {
        case ADD:
            return {count: state.count + 1} //change whole object instead of using state.count + 1, return state
        default:
            return state
    }
}

